'use strict';

let modalComponent = {
  id: null,
  title: 'Add New Todo',
  value: '',
  renderHTML() {

    const html = `
      <div class="modal" id="modal">
        <div class="modal-header">
          <h4 class="modal-title"></h4>
          <button type="button" class="close btn btn-red" id="btn-close-modal">
            <span aria-hidden="true">X</span>
          </button>
        </div>
        <div class="modal-body">
            <div>
              <input type="text" placeholder="${this.title}" id="input-add-todo" class="form-control" id="" value="${this.value}" required>
            </div>
            <div>
            <button type="button" id="btn-save-todo" class="btn btn-green">Submit</button>
            </div>
        </div>
      </div>
      
    `;

    return html.trim();
  }
};

function renderTable(data) {

  // Remove unpublished/deleted items from the array
  const dataIsolated = data.filter(p => p.publish);

  let rows = [];
  dataIsolated.map(items => {
    
    const btns = `
      <button type="button" class="btn btn-blue btn-complete">Edit</button>
      <button type="button" class="btn btn-red btn-delete">Delete</button>
    `;

    rows += `
      <tr>
        <td>${items.name}</td>
        <td>${items.priority}</td>
        <td>${items.complete}</td>
        <td>${btns}</td>
      </tr>
    `;
  });

  const addBtn = `
    <div class="text-center">
      <button type="button" id="btn-launch-modal" class="btn btn-green">Add New Todo</button>
    </div>
  `;

  const table = `
    ${addBtn}
    <table class="table">
      <thead>
        <tr>
          <th>Name</th>
          <th>Priority</th>
          <th>Complete</th>
          <th>Actions</th>
        </tr>
      </thead>
      <tbody>
        ${rows}
      </tbody>
    </table>
  `;

  return table;
}

let data = [
  {
    id: 1,
    name: 'Take out the trash',
    priority: 0,
    complete: 'No',
    publish: true
  }, {
    id: 2,
    name: 'Bring in the trash',
    priority: 0,
    complete: 'No',
    publish: true
  },  {
    id: 3,
    name: 'Walk the dog',
    priority: 0,
    complete: 'No',
    publish: false
  } 
];

/* ########################
######### METHODS #########
######################### */



const closeModal = () => {
  // Remove modal from DOM
  const modal = document.getElementById('modal');
  modal.remove();
};

const renderModal = () => {
  // Generate new modal object
  const modal = Object.create(modalComponent);
  modal.title = 'Add New Todo';
  const newModal = modal.renderHTML();
  const mount = document.getElementById('modal-mount-point');
  // Add modal to the DOM
  mount.innerHTML = newModal;

  // Automatically add focus to the input with the modal
  const input = document.getElementById('input-add-todo');
  input.focus();

  // Add listener to close modal button
  document.getElementById("btn-close-modal").addEventListener("click", closeModal);

  // add lister to save btn
  document.getElementById("btn-save-todo").addEventListener("click", saveValue);

};

const saveValue = (e) => {
 
  const value = document.getElementById("input-add-todo").value;

  data.push({
    id: '',
    name: value,
    priority: 0,
    complete: 'No',
    publish: true
  });

  console.log(data);
};

const loadApp = () => {
  
  const tableHTML = renderTable(data);
  const mount = document.getElementById('app-mount-point');

  mount.innerHTML = tableHTML;

  // Listen for add todo click
  document.getElementById("btn-launch-modal").addEventListener("click", renderModal);

};

document.addEventListener("DOMContentLoaded", loadApp);
